import { ClubProvider } from './../../../providers/club-provider';
import { PageBusiness } from './../../page.d';
import { DEFAULT_LOGO } from './../../../services/constants';
import { PreloadProvider } from './../../../providers/preload-provider';
import { Clipboard } from '@ionic-native/clipboard'; 
import { ShareMessage } from './../../../providers/share-provider';
import { NavParams  } from 'ionic-angular';
import { Session } from './../../../services/session';
import { Utility } from './../../../services/utility';

import { Component, ViewChild, ChangeDetectorRef, ElementRef } from '@angular/core';
import { IonicPage, ModalController, NavController, Events, ActionSheetController, AlertController, Content } from 'ionic-angular';
import { DomSanitizer } from '@angular/platform-browser';
//import { Item } from '../../../models/item';
import { Message, Widget } from '../../../models/models';
import { MessageProvider, CacheProvider } from '../../../providers/providers';
import { TranslateService } from '@ngx-translate/core';
import { WidgetProvider } from '../../../providers/providers';
import { Webpage } from '../../../models/models';
import { StorageService } from '../../../services/services';
@IonicPage()
@Component({
  selector: 'page-message-list',
  templateUrl: 'message-list.html'
})
export class MessageListPage implements PageBusiness{
  messages: Message[] = []; //显示信息数组
  finiteScroll: Boolean; //是否还有更多数据   
  timer: any;
  messageTitle: string = ''; 
  topId: string;
  isDataLoading: boolean = false; //是否在加载数据
  isDataEmpty: boolean = false; // 是否为无数据 
  page: number; // 页码
  pageCount:  number = 10; // 每页条数   
  currentWidget: Widget;
  @ViewChild(Content) content: Content;
  constructor(public navCtrl: NavController,
    private actionSheetCtrl: ActionSheetController,
    public events: Events,
    public messageProvider: MessageProvider,
    public modalCtrl: ModalController,
    public alertCtrl: AlertController,
    public widgetProvider: WidgetProvider,
    public translateService: TranslateService,
    public cd: ChangeDetectorRef,
    public session: Session,
    private sanitizer: DomSanitizer,
    public navParams: NavParams, 
    public clipboard: Clipboard,
    public preload: PreloadProvider,
    public cacheProvider: CacheProvider,
    public element: ElementRef,
    public clubProvider: ClubProvider,
    public storage: StorageService) {

    this.messageTitle = this.navParams.data.widgetName;
    this.session.messageWidgetId = this.navParams.data.widgetId; 
    this.getCurrentWidget()
    if(this.navParams.data.messages){
      this.messages.push.apply(this.messages,this.navParams.data.messages.reverse())

      this.finiteScroll = this.messages.length >= this.pageCount 
      this.refreshDisplaytime()
      this.transHtml()
    }  else{
      this.createArray();
    }
  }

  async getCurrentWidget(){
    this.currentWidget = this.widgetProvider.getWidgetById(this.session.messageWidgetId); 

    if(!this.currentWidget){
      // if()
      let site = ""
      if (this.session.user != null) {
        site = this.session.user.employeeInfo.siteCode; 
      }

      let siteCode = await this.storage.getItem('siteCode');
      if (siteCode) {
        site = siteCode
      }

      let channel = this.navParams.data.extra.fromType
     
      let Communitys = ['board','video','club']
      let isBoard = Communitys.some(item => item == channel.toLowerCase())

      if(isBoard){
        let widget: Widget = {
          id:  this.navParams.data.widgetId,
          widgetName: this.navParams.data.sessionName,
          appPageName: '',
          iconUrl: this.navParams.data.widgetIcon,
          homeUrl: this.clubProvider.BoardDetail(this.navParams.data.widgetId, channel, site) 
        }

        this.currentWidget = widget
      }
    }
  }
  
  
  openWidget(widget: Widget) { 
    let page : Webpage = {
      url: widget.appPageName || widget.homeUrl,  // 链接地址
      title: widget.widgetName,  // 标题
      description: "",   // 摘要内容
      imageUrl: widget.iconUrl,   // 图片路径
      appId: widget.id.toString() , // 應用ID
      options:{
        canShare: widget.canShare,
        allowCopyLink: widget.allowCopyLink,
        allowOpenInBrowser: widget.allowOpenInBrowser,
        useNativeBrowser: widget.useNativeBrowser
      }
    };
    this.widgetProvider.openWebpage(page ,this.navCtrl); 
}

  ionViewDidLoad(){
    this.events.subscribe('lang:changed', () => {
      this.refreshDisplaytime()
    })
    //登录后刷新消息
    this.events.subscribe('user:logined', (time) => {
      this.createArray();
    }); 

    this.events.subscribe("messageProvider-addMessage", message=>{
      // this.messages.unshift(message); 
      this.createArray()
      this.refreshDisplaytime();
    }); 
  }

  ionViewWillUnload(){  
    this.events.unsubscribe("messageProvider-addMessage"); 
  }

  /**
   * The view loaded, let's query our items for the list
   */
  ionViewWillEnter() { 
    this.setMessageTimer(); 
  }
 
  
  ionViewDidLeave() {  
    clearTimeout(this.timer);
  }
  
  // 下拉加载更多
  doRefresh(_event) { 
    this.messages = []
    this.createArray(_event)
  }

  doInfinite(_event) { 
    this.createArray(_event)
  }
  /**
   * 加载数据
   * @param _event derefresh事件，加载更多数据
   */
  createArray(_event?) { 
    this.isDataLoading = true;
    this.messageProvider.queryMessage( this.session.messageWidgetId , this.pageCount, this.messages? this.messages.length : 0, true).subscribe(res => { 
      if(res){  

        if(res.length == this.pageCount){
          this.finiteScroll = true
        }else {
          this.finiteScroll = false
        }
        this.isDataLoading = false;
        this.messages.push.apply(this.messages, res); 
        this.refreshDisplaytime()
        this.transHtml() 
      }
     
    },err=>{
      this.isDataEmpty = true;
      this.isDataLoading = false;
    })
    _event && _event.complete()
  }


  scrollTopByHandle() {
    if(!this.topId)return;
    let ele = this.element.nativeElement.querySelector(`#${this.topId}`);
    this.content.scrollTop = ele.offsetTop > 20 ? ele.offsetTop - 20 : ele.offsetTop
  }
  released(){
    
  }

  options(){ 
    let sheet = null; 
    let buttons = [];
    if(!!this.currentWidget){
      buttons.push({
        text: this.translateService.instant('APP_COMMON_ENTER') + ' ' + this.currentWidget.widgetName,
        handler: () => {
          this.openWidget(this.currentWidget)
        }
      })
    }
    buttons.push({
      text: this.translateService.instant('APP_COMMON_CLEAR_MESSAGES'),
      handler: () => {
        this.clearMessage()
      }
    })
    buttons.push({
      text: this.translateService.instant('MESSAGE_DELETE_CANCEL'),
      role: 'cancel'
    }) 
    sheet = this.actionSheetCtrl.create({ buttons: buttons }); 
    sheet.present() 
  }
  // 长按弹出菜单
  onHold(message: Message, _event) {   
    _event.target.classList.add('img-scaling')
    let that = this;
    let sheet = null;

    let buttons = [];
    buttons.push({
      text: that.translateService.instant('MESSAGE_DELETE'),
      handler: () => {
        that.showDeleteMsg(message);
      }
    })

    if( message.formatType.indexOf('Image') > -1){
      buttons.push({
        text: that.translateService.instant('APP_COMMON_RELOAD_IAMGE'),
        handler: () => {
          that.cacheProvider.reloadImage(message.imageUrl).subscribe()
          message.imageUrl =  message.imageUrl + "#reload"
        }
      })
    }

    if (message.redirectUrl || message.formatType.indexOf('Image') > -1) {

      buttons.push({
        text: that.translateService.instant('SHARE_MODAL_TITLE'),
        handler: () => { 
          that.openSharePage(message);
        }
      })

    } else {
      buttons.push({
        text: that.translateService.instant('APP_COMMON_COPY_MESSAGE'),
        handler: () => {
          this.clipboard.copy(message.abstract)
        }
      })
    }

    buttons.push({
      text: that.translateService.instant('MESSAGE_DELETE_CANCEL'),
      role: 'cancel'
    })
     
    //长按时同时识别图片中是否存在二维码，若存在，则增加 识别二维码选项
    this.cacheProvider.decodeQRCode(message.imageUrl).subscribe(decodeResult => {
      buttons.splice(buttons.length - 1, 1, {
        text: that.translateService.instant('APP_COMMON_EXTRACT_QRCODE'),
        handler: () => {
          that.openQRCodeUrl(decodeResult, message);
        }
      })
      buttons.push({
        text: that.translateService.instant('MESSAGE_DELETE_CANCEL'),
        role: 'cancel'
      })

      let newsheet = that.actionSheetCtrl.create({ buttons: buttons });
      newsheet.present();
      sheet && sheet.dismiss()
      newsheet.onDidDismiss(()=>{ 
        _event.target.classList.remove('img-scaling')
      })

    },err=>{
      sheet = that.actionSheetCtrl.create({ buttons: buttons });
      sheet.present();
      sheet.onDidDismiss(()=>{ 
        _event.target.classList.remove('img-scaling')
      })
     })
 
  }

  /**
  * 打开分享页面
  */
  openSharePage(message: Message) {

    let shareMessage = {
      title: message.title, //标题
      description: message.abstract, //描述
      thumb: message.imageUrl || DEFAULT_LOGO, //缩略图
      resource: message.redirectUrl || message.imageUrl //图片url或网页url
    }
  

    this.preload.openShareModal({
      shareMessage: shareMessage,
      callback:(data)=>{
        if(data){
          console.log(data)
        }
      },
      isShareImage:message.redirectUrl ? false : true ,
      navigationDockTargetMapIds: [1, 2]
      // navigationDockTargetMapIds: [1, 2, 7]
    })
  }

 

  /**
   * 打开二维码地址
   * @param imgMsg 二维码url
   * @param message Message对象
   */
  openQRCodeUrl(imgMsg, message) {
    let page: Webpage = {
      url: imgMsg,  // 链接地址
      title: message.title, // 标题
      description: message.abstract,   // 摘要内容
      imageUrl: message.imageUrl,   // 图片路径
      appId: message.widgetId.toString(),  // 應用ID
      options:{
        canShare: true
      }
    };
    this.widgetProvider.openWebpage(page, this.navCtrl, true);
  }



  /**
   * 创建分享对象
   * @param message 消息对象
   */
  buildMessageForWechatShare(message: Message) {
    let shareMessage: ShareMessage = {
      title: message.title || message.abstract || message.formatType,
      description: message.abstract,
      thumb: message.widgetIcon,
      resource: message.imageUrl
    }
    return shareMessage
  }
  /**
   * Navigate to the detail page for this item.
   */
  goMessageDetail(message: Message, replaceUrl?: string) {

    message.isRead = true;
    this.messageProvider.setMessageIsRead(message.id);
    if (!message.redirectUrl && !replaceUrl) return;
    let page: Webpage = {
      url: replaceUrl || message.redirectUrl,  // 链接地址
      title: message.title == null ? message.widgetName : message.title, // 标题
      description: message.abstract,   // 摘要内容
      imageUrl: message.imageUrl,   // 图片路径
      appId: message.widgetId == null ? '' : message.widgetId.toString(),  // 應用ID
      options:{}
    };
    this.widgetProvider.openWebpage(page, this.navCtrl);
  }
 

    //清空消息
    clearMessage() {
      let confirm = this.alertCtrl.create({
        title: this.translateService.instant('APP_COMMON_HANDLE_TIPS'),
        message: this.translateService.instant('MESSAGE_CLEAR_MESSAGE'),
        buttons: [
          {
            text: this.translateService.instant('MESSAGE_DELETE_CANCEL'),
            handler: () => {
            }
          },
          {
            text: this.translateService.instant('MESSAGE_DELETE_CONFIRM'),
            handler: () => { 
              this.messages = [];
              this.isDataEmpty = true;
              this.messageProvider.deleteMessageBySessionId(this.session.messageWidgetId); 
            }
          }
        ]
      });
      confirm.present();
    }
 

  //显示删除alert
  showDeleteMsg(message) {
    let confirm = this.alertCtrl.create({
      title: this.translateService.instant('MESSAGE_DELETE_TITLE'),
      message: this.translateService.instant('MESSAGE_DELETE_MESSAGE'),
      buttons: [
        {
          text: this.translateService.instant('MESSAGE_DELETE_CANCEL'),
          handler: () => {
          }
        },
        {
          text: this.translateService.instant('MESSAGE_DELETE_CONFIRM'),
          handler: () => {
            let index = this.messages.findIndex(p => p.id == message.id);
            this.messages.splice(index, 1);
            this.messageProvider.deleteMessage(message.id);
            //this.getMessages(); 
          }
        }
      ]
    });
    confirm.present();
  }
 
  //一分钟刷新一次显示时间
  setMessageTimer() {
    this.timer = setInterval(() => {
      this.refreshDisplaytime()
    }, 1000 * 60)
  }

  refreshDisplaytime() {
    for (let item of this.messages) {
      item.displayTime = Utility.formatMessageTime(this.session.language, item.receiveTime)
    }
  }

  lowerCaseFormatType(type: string){
    if(!type)return""
    return type.toLowerCase()
  }
  transHtml() {
    for (let item of this.messages) {
      if (item.abstract) {
        item.displayAbstract = this.messageProvider.transformHtml(item.abstract);
        item.displayAbstract = this.sanitizer.bypassSecurityTrustHtml(item.displayAbstract); //遵循angular机制才会正常绑定html 
      }
    }
  }

  abstractOnClick(e, message: Message) {
    e.stopPropagation() //阻止冒泡事件 
    e = e || window.event; //兼容IE8
    let target = e.target || e.srcElement;  //判断目标事件 
    const url = target.href === 'javascript:void(0)' ? (target.title || target.innerText) : ''
    this.goMessageDetail(message, url)
  }

  /**
   * 滚动到底部
   */
  scrollToBottom() {
    setTimeout(() => {
      this.content.scrollToBottom();
    }, 10);
  }
 
}



